@font-face {
  font-family: 'Roboto';
  src: url(../fonts/Roboto-Regular.woff2), url(../fonts/Roboto-Regular.woff);
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Roboto';
  src: url(../fonts/Roboto-Bold.woff2), url(../fonts/Roboto-Bold.woff2);
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ul,
ol,
dl {
  list-style: none;
}
img {
  max-width: 100%;
}
.page__body {
  max-width: 100%;
  min-width: 320px;
  font-family: 'Roboto', 'Arial', sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #424242;
  background-color: #EDE8E2;
  overflow-x: hidden;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  max-width: 1740px;
  margin: 0 auto;
  padding: 0 17px;
}
@media (min-width: 744px) {
  .container {
    padding: 0 61px;
  }
}
@media (min-width: 1400px) {
  .container {
    padding: 0 117px;
  }
}
@media (min-width: 1920px) {
  .container {
    padding: 0;
  }
}
.main__caption {
  font-size: 24px;
  font-weight: 700;
  line-height: 28px;
}
.main__caption span {
  color: #8B9D83;
}
@media (min-width: 1400px) {
  .main__caption {
    font-size: 36px;
    line-height: 42px;
  }
}
@media (min-width: 1920px) {
  .main__caption {
    font-size: 48px;
    line-height: 56px;
  }
}
.foreword {
  width: 283px;
}
.foreword h2 {
  margin-bottom: 20px;
}
@media (min-width: 744px) {
  .foreword {
    display: flex;
    align-items: center;
    gap: 40px;
    width: 100%;
  }
  .foreword h2 {
    margin: 0;
  }
  .foreword p {
    width: 80%;
  }
}
@media (min-width: 1400px) {
  .foreword {
    gap: 200px;
  }
  .foreword p {
    font-size: 24px;
    line-height: 28px;
  }
}
@media (min-width: 1920px) {
  .foreword {
    gap: 290px;
  }
  .foreword p {
    font-size: 28px;
    line-height: 32px;
  }
}
.primary-button {
  display: block;
  width: 150px;
  padding: 17px 0;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  background-color: #8B9D83;
  border: none;
  border-radius: 58px;
  cursor: pointer;
}
.primary-button:hover {
  background-color: #6D8E5E;
}
.primary-button:active {
  background-color: #8B9D83;
}
.primary-button:disabled {
  background-color: #dcdcdc;
  cursor: default;
}
.primary-button--arrow {
  position: relative;
  padding-right: 35px;
}
.primary-button--arrow::before {
  content: "";
  position: absolute;
  top: calc(50% - 7.5px);
  right: 18px;
  width: 17px;
  height: 15px;
  background: url('../img/icon-arrow-right.svg') no-repeat 50%;
}
.secondary-button {
  display: block;
  min-width: 150px;
  padding: 15px 0;
  font-size: 16px;
  line-height: 18px;
  text-align: center;
  color: #686656;
  background-color: transparent;
  border: 2px solid #8B9D83;
  border-radius: 55px;
  cursor: pointer;
}
.secondary-button:hover {
  border-color: #6D8E5E;
}
.secondary-button:active {
  border-color: #8B9D83;
}
.secondary-button:disabled {
  border-color: #dcdcdc;
  cursor: default;
}
.secondary-button--arrow {
  position: relative;
  padding-left: 35px;
}
.secondary-button--arrow::before {
  content: "";
  position: absolute;
  top: calc(50% - 7.5px);
  left: 18px;
  width: 17px;
  height: 15px;
  background: url('../img/icon-arrow-left.svg') no-repeat 50%;
}
.input {
  appearance: none;
  height: 45px;
  padding: 15px;
  font-style: inherit;
  color: #515151;
  border: 2px solid #D5D5D5;
  border-radius: 17px;
}
.input:hover,
.input:focus {
  border-color: #6D8E5E;
}
.input:focus:invalid {
  border-color: #CF0909;
}
.input:focus-visible {
  outline: none;
}
.footer {
  width: 100%;
  border: 5px solid #8B9D83;
}
.footer__wrapper {
  display: flex;
  flex-direction: column;
  gap: 60px;
  padding: 60px 0 30px;
}
@media (min-width: 744px) {
  .footer__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: space-between;
    gap: 30px;
    padding: 100px 60px 70px;
  }
}
@media (min-width: 1400px) {
  .footer__wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 0;
    padding: 75px 120px;
  }
}
@media (min-width: 1920px) {
  .footer__wrapper {
    padding: 90px;
  }
}
.footer__logo-container {
  max-width: fit-content;
  margin: 0 auto;
  order: 6;
}
@media (min-width: 744px) {
  .footer__logo-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: space-between;
    gap: 20px 0;
    grid-column: 1/3;
    width: 100%;
    max-width: none;
    margin: 0;
  }
}
@media (min-width: 1400px) {
  .footer__logo-container {
    display: flex;
    flex-direction: column;
    gap: 17px;
    width: fit-content;
    order: 0;
  }
}
@media (min-width: 1920px) {
  .footer__logo-container {
    gap: 25px;
  }
}
.footer__link {
  display: block;
  margin: 0 auto 15px;
  width: 200px;
  height: auto;
}
@media (min-width: 744px) {
  .footer__link {
    grid-row: 1/3;
    align-self: end;
    margin: 0;
  }
}
@media (min-width: 1400px) {
  .footer__link {
    align-self: start;
  }
}
.footer__slogan {
  margin-bottom: 30px;
  font-size: 14px;
  line-height: 16px;
}
@media (min-width: 744px) {
  .footer__slogan {
    align-self: end;
    width: 225px;
    margin: 0;
    margin-left: auto;
  }
}
@media (min-width: 1400px) {
  .footer__slogan {
    align-self: start;
    width: auto;
    margin: 0;
  }
}
@media (min-width: 1920px) {
  .footer__slogan {
    font-size: 16px;
    line-height: 18px;
  }
}
.footer__company {
  color: #A49B9B;
}
@media (min-width: 744px) {
  .footer__company {
    align-self: start;
    width: 225px;
    margin-left: auto;
  }
}
@media (min-width: 1400px) {
  .footer__company {
    width: auto;
    margin: 0;
  }
}
@media (min-width: 1920px) {
  .footer__company {
    font-size: 20px;
    line-height: 23px;
  }
}
.footer__menu {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
  align-items: center;
  gap: 23px 70px;
  margin: 0 auto;
}
.footer__menu a {
  display: block;
  width: 100%;
  height: auto;
  font-size: 20px;
  line-height: 23px;
  text-decoration: none;
  text-align: center;
  color: #424242;
}
.footer__menu a:hover {
  color: #6D8E5E;
}
.footer__menu a:active {
  color: #8B9D83;
}
@media (min-width: 744px) {
  .footer__menu a {
    text-align: left;
  }
}
@media (min-width: 1920px) {
  .footer__menu a {
    font-size: 24px;
    line-height: 28px;
  }
}
@media (min-width: 744px) {
  .footer__menu {
    margin: 0;
    max-width: 300px;
  }
}
@media (min-width: 1400px) {
  .footer__menu {
    margin-left: 230px;
  }
}
@media (min-width: 1920px) {
  .footer__menu {
    gap: 30px 80px;
    margin-left: 160px;
  }
}
.footer__contacts {
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 25px;
  width: fit-content;
  margin: 0 auto;
}
@media (min-width: 744px) {
  .footer__contacts {
    margin: 0;
    margin-left: auto;
  }
}
.footer__phone,
.footer__mail,
.footer__adress {
  position: relative;
  width: fit-content;
  padding-left: 35px;
  font-size: 20px;
  line-height: 23px;
  text-decoration: none;
  color: #424242;
}
.footer__phone:hover,
.footer__mail:hover,
.footer__adress:hover {
  color: #6D8E5E;
}
.footer__phone:active,
.footer__mail:active,
.footer__adress:active {
  color: #8B9D83;
}
.footer__phone::before,
.footer__mail::before,
.footer__adress::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 23px;
  height: 22px;
  background: no-repeat 50% / contain;
}
@media (min-width: 1920px) {
  .footer__phone::before,
  .footer__mail::before,
  .footer__adress::before {
    width: 30px;
    height: 29px;
  }
}
@media (min-width: 1920px) {
  .footer__phone,
  .footer__mail,
  .footer__adress {
    padding-left: 45px;
    font-size: 24px;
    line-height: 28px;
  }
}
.footer__phone::before {
  background-image: url('../img/icon-phone.svg');
}
.footer__mail::before {
  background-image: url('../img/icon-mail.svg');
}
.footer__adress::before {
  background-image: url('../img/icon-geo.svg');
}
.header {
  position: relative;
  margin-bottom: 27px;
  padding: 16px;
  background-color: #EDE8E2;
}
@media (min-width: 744px) {
  .header {
    margin-bottom: 59px;
    padding-top: 36px;
  }
}
@media (min-width: 1400px) {
  .header {
    margin-bottom: 71px;
  }
}
@media (min-width: 1920px) {
  .header {
    margin-bottom: 133px;
  }
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: calc(1400px - 200px)) {
  .header__wrapper {
    justify-content: flex-start;
    gap: 70px;
  }
}
.header__logo {
  width: 132px;
  height: auto;
}
.header__logo img {
  max-width: 100%;
}
@media (min-width: 744px) {
  .header__logo {
    width: 200px;
  }
}
.header__nav {
  display: none;
}
@media (min-width: calc(1400px - 200px)) {
  .header__nav {
    display: initial;
  }
}
.nav__list {
  max-width: 250px;
  margin: 130px 0 0 80px;
}
@media (min-width: calc(1400px - 200px)) {
  .nav__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 40px;
    max-width: none;
    margin: 0;
  }
}
.nav__item {
  display: block;
  width: fit-content;
  height: 25px;
  margin-bottom: 40px;
}
@media (min-width: calc(1400px - 200px)) {
  .nav__item {
    width: auto;
    margin: 0;
  }
}
.nav__link {
  display: block;
  max-width: 100%;
  font-size: 20px;
  line-height: 23px;
  text-decoration: none;
  color: #424242;
}
.nav__link:hover {
  color: #6D8E5E;
}
.nav__link:active {
  color: #8B9D83;
}
@media (min-width: 1920px) {
  .nav__link {
    font-size: 24px;
    line-height: 28px;
  }
}
.header__phone {
  margin-left: auto;
  z-index: 5;
}
.header__button {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 28px;
  height: 16px;
  border: none;
  background: none;
  cursor: pointer;
}
.header__button:hover .header__burger {
  background-color: #6D8E5E;
}
.header__button:active .header__burger {
  background-color: #8B9D83;
}
@media (min-width: 744px) {
  .header__button {
    width: 49px;
    height: 30px;
  }
}
@media (min-width: calc(1400px - 200px)) {
  .header__button {
    display: none;
  }
}
.header__burger {
  width: 100%;
  height: 2px;
  background-color: #424242;
  transition: all 0.3s ease;
}
.header__burger--third {
  width: 50%;
}
@media (min-width: 744px) {
  .header__burger {
    height: 3px;
  }
}
.header__button--cross .header__burger--first {
  transform: translateY(7px) rotate(45deg);
}
@media (min-width: 744px) {
  .header__button--cross .header__burger--first {
    transform: translateY(14px) rotate(45deg);
  }
}
.header__button--cross .header__burger--second {
  transform: rotate(-45deg);
}
.header__button--cross .header__burger--third {
  width: 50%;
  opacity: 0;
}
.header__phone-link {
  display: none;
}
@media (min-width: calc(1400px - 200px)) {
  .header__phone-link {
    position: relative;
    display: initial;
    padding-left: 43px;
    font-size: 20px;
    text-decoration: none;
    color: #424242;
  }
  .header__phone-link:hover {
    color: #6D8E5E;
  }
  .header__phone-link:active {
    color: #8B9D83;
  }
  .header__phone-link::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 25px;
    height: 24px;
    background: url('../img/icon-phone.svg') no-repeat center;
  }
}
@media (min-width: 1920px) {
  .header__phone-link {
    font-size: 24px;
    line-height: 28px;
  }
}
.header__popup {
  position: absolute;
  top: 0;
  right: 0;
  width: 320px;
  height: 640px;
  background-color: #EDE8E2;
  overflow: hidden;
  z-index: 1;
  transform: translateY(-100%);
  transition: all 0.4s ease-out;
}
.header__popup--active {
  transform: translateY(0);
  transition: all 0.4s ease-in;
}
@media (min-width: calc(1400px - 200px)) {
  .header__popup {
    display: none;
  }
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  visibility: hidden;
  opacity: 0;
  transition: all 0.2s ease-in-out;
  backdrop-filter: blur(2px);
  z-index: 100;
}
.modal--active {
  visibility: visible;
  opacity: 1;
}
.modal--active .modal__wrapper {
  transform: scale(1);
}
.modal__wrapper {
  position: relative;
  width: 100%;
  max-width: 320px;
  min-height: 400px;
  padding: 50px 0;
  background-color: #EDE8E2;
  border: 2px solid #8B9D83;
  border-radius: 20px;
  transform: scale(0);
  transition: all 0.3s ease-in;
}
@media (min-width: 744px) {
  .modal__wrapper {
    max-width: 520px;
    min-height: 330px;
  }
}
@media (min-width: 1400px) {
  .modal__wrapper {
    max-width: 780px;
    min-height: 500px;
    padding: 75px 0;
    border-radius: 30px;
  }
}
@media (min-width: 1920px) {
  .modal__wrapper {
    padding: 75px 0;
  }
}
.modal__caption {
  margin-bottom: 30px;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
}
@media (min-width: 1400px) {
  .modal__caption {
    margin-top: 15px;
    font-size: 24px;
    line-height: 28px;
  }
}
@media (min-width: 1920px) {
  .modal__caption {
    margin: 0;
    margin-bottom: 35px;
    font-size: 32px;
    line-height: 37px;
  }
}
.modal__form {
  width: 240px;
  margin: 0 auto;
}
@media (min-width: 1400px) {
  .modal__form {
    width: 360px;
  }
}
.modal__input {
  width: 100%;
  height: 45px;
  margin-bottom: 8px;
}
@media (min-width: 1400px) {
  .modal__input {
    height: 70px;
    padding-left: 25px;
    padding-right: 25px;
    margin-bottom: 12px;
    font-size: 21px;
    line-height: 25px;
    border-radius: 26px;
  }
}
.modal__button {
  width: 230px;
  height: 40px;
  padding: 12px 0;
  padding-right: 35px;
  margin: 0 auto;
  margin-top: 12px;
}
@media (min-width: 1400px) {
  .modal__button {
    width: 320px;
    height: 60px;
    font-size: 21px;
    line-height: 25px;
    border-radius: 68px;
  }
}
.modal__cross {
  position: absolute;
  top: 20px;
  right: 25px;
  width: 15px;
  height: 15px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.modal__cross:hover path {
  stroke: #6D8E5E;
}
.modal__cross:active path {
  stroke: #8B9D83;
}
@media (min-width: 1400px) {
  .modal__cross {
    top: 30px;
    right: 35px;
    width: 23px;
    height: 23px;
  }
}
.process {
  position: relative;
}
@media (min-width: 744px) {
  .process::after {
    content: "";
    position: absolute;
    top: 80%;
    left: 0;
    width: 100%;
