  }
}
@media (min-width: 1400px) {
  .process::after {
    top: 70%;
  }
}
.process__foreword {
  margin-bottom: 50px;
}
@media (min-width: 744px) {
  .process__foreword {
    margin-bottom: 85px;
  }
}
@media (min-width: 1400px) {
  .process__foreword {
    margin-bottom: 100px;
  }
}
@media (min-width: 1920px) {
  .process__foreword {
    margin-bottom: 120px;
  }
}
.process__list {
  margin-bottom: 40px;
}
@media (min-width: 744px) {
  .process__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 46px 67px;
    margin: 0;
