}
@media (min-width: 1920px) {
  .process__list {
    gap: 125px 180px;
  }
}
.process__wrapper {
  width: 100%;
  height: auto;
  margin-bottom: 80px;
}
@media (min-width: 744px) {
  .process__wrapper {
    margin-bottom: 170px;
  }
}
@media (min-width: 1400px) {
  .process__wrapper {
    margin-bottom: 200px;
  }
