.projects {
  margin-bottom: 125px;
  background: url('../img/bg-forest.svg') no-repeat;
  background-size: 100% auto;
  background-position: 0 100%;
}
@media (min-width: 744px) {
  .projects {
    margin-bottom: 120px;
    background: none;
  }
}
@media (min-width: 1400px) {
  .projects {
    margin-bottom: 140px;
  }
}
