.quiz {
  position: relative;
  margin-bottom: 40px;
  overflow-x: hidden;
}
.quiz::before {
  content: "";
  position: absolute;
  top: 73%;
  left: -23%;
  width: 321px;
  height: 308px;
  background: url('../img/bg-wood.svg') no-repeat;
  background-size: contain;
  transform: rotate(80deg);
  z-index: -1;
}
@media (min-width: 744px) {
  .quiz {
    margin-bottom: 105px;
  }
  .quiz::before {
    top: 77%;
    left: -5%;
    width: 474px;
    height: 456px;
    transform: rotate(225deg);
  }
  .quiz::after {
    content: "";
    position: absolute;
    top: 4%;
    right: -3%;
    width: 340px;
    height: 330px;
    background: url('../img/bg-wood.svg') no-repeat;
    background-size: contain;
    transform: rotate(85deg);
    z-index: -1;
  }
}
@media (min-width: 1400px) {
  .quiz {
    margin-bottom: 155px;
  }
  .quiz::before {
    top: 68%;
    left: 0;
    width: 452px;
    height: 426px;
    transform: rotate(-90deg);
  }
  .quiz::after {
    top: 18%;
    width: 326px;
    height: 307px;
    transform: rotate(250deg);
  }
}
@media (min-width: 1920px) {
  .quiz {
    margin-bottom: 115px;
  }
  .quiz::before {
    top: 80%;
    left: 8%;
    width: 356px;
    height: 341px;
    transform: rotate(-70deg);
  }
  .quiz::after {
    top: 8%;
    right: -10%;
    width: 481px;
    height: 453px;
    transform: rotate(235deg);
  }
}
